import React, { Component } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';

//import all images
import cat1 from "./img/cat1.jpg";
import cat2 from "./img/cat2.jpg";
import cat3 from "./img/cat3.jpg";
import cat4 from "./img/cat4.jpg";
import cat5 from "./img/cat5.jpg";
import bee1 from "./img/bee1.png";
import bee2 from "./img/bee2.jpg";
import bee3 from "./img/bee3.jpeg";
import bee4 from "./img/bee4.jpg";
import bee5 from "./img/bee5.jpg";


class App extends Component{
  constructor(props) {
   super(props);
   this.state = {
     selectValue: 0,
     iList: [cat1, bee1, cat2, bee2, cat3, cat4, bee3, cat5, bee4, bee5],
     ansList: ["cat", "bee", "cat", "bee", "cat", "cat", "bee", "cat", "bee", "bee"],
     numRight: 0,
     numAns: 0,
     showGen: false,
     showCheck: false,
   };

   this.handleDropdownChange = this.handleDropdownChange.bind(this);
   this.handleCat = this.handleCat.bind(this);
   this.handleBee = this.handleBee.bind(this);
 }

 handleDropdownChange(e) {
   this.setState({
     selectValue: parseInt(e.target.value, 10),
     showGen: true,
      });
    }

  handleCat(i) {
    var addr = 0;
    if("cat" == this.state.ansList[i]){
      addr = 1;
    }
    this.setState({
      numAns: this.state.numAns+1,
      numRight: this.state.numRight+addr,
    });
    if(this.state.numAns == this.state.selectValue){
      alert("You got " + this.state.numRight + "/" + this.state.selectValue +", or " + Math.round(this.state.numRight/this.state.selectValue * 100) + '%')
    }
  }


  handleBee(i) {
    var addr = 0;
    if("bee" == this.state.ansList[i]){
      addr = 1;
    }
    this.setState({
      numAns: this.state.numAns+1,
      numRight: this.state.numRight+addr,
    });
    if(this.state.numAns == this.state.selectValue){
      alert("You got " + this.state.numRight + "/" + this.state.selectValue +", or " + Math.round(this.state.numRight/this.state.selectValue * 100) + '%')
    }
  }


 render () {
   var questions = []

     for(let i = 0; i < this.state.selectValue; i++){
      questions.push(
       <div class="card" style={{width: 500, margin: 20}}>
         <img class="card-img-top" src={this.state.iList[i]} alt="Card image cap"/>
          <div class="card-body">
            <h5 class="card-title">What Animal is This?</h5>
            <div>
              <button type="button" class="btn btn-secondary" onClick={() => this.handleCat(i)}>cat</button>
              <button type="button" class="btn btn-warning" onClick={() => this.handleBee(i)}>bee</button>
         </div>
        </div>
      </div>
     );
   }

   return (
     <div>
       <div style={{display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center'}}>
       <h1> Bee/Cat quiz </h1>
         <h3>How many quiz images?</h3>
          <div>
             <select id="dropdown" onChange={this.handleDropdownChange}>
               <option value="0">0</option>
               <option value="1">1</option>
               <option value="2">2</option>
               <option value="3">3</option>
               <option value="4">4</option>
               <option value="5">5</option>
               <option value="6">6</option>
               <option value="7">7</option>
               <option value="8">8</option>
               <option value="9">9</option>
               <option value="10">10</option>
             </select>
          <div>
            Selected value is : {this.state.selectValue}
           </div>
          </div>
            {questions}

            <button type="button" class="btn btn-dark"  onClick={() => this.handleCat(0)}>Check Answers</button>
            <p>This site is horribly broken and I did not account for any corner cases, so as long as you use it only as expect everything should work</p>
       </div>
     </div>
    );
  }
}




export default App;
